declare var $rg_index: (options: {
  iframe: HTMLIFrameElement
  origin: string
  U: any
}) => void
declare var sdk_version: string
declare var app_id: string
declare var adv_channel: string
declare var sdk_origin: string
declare var sdk_origin_test: string
declare var sdk_hash: string

interface Window {
  $rg_main: any
}
{
  require('./style.css' as any);

  class UrlParam {
    urlParamKeyArr: string[] = []
    urlParamMap: any = {}
    constructor() {
      let interrogationIndex = location.href.indexOf("?") + 1
      let str = interrogationIndex === 0 ? "" : location.href.slice(interrogationIndex)
      if (str) {
        let arr = str.split(/&|%26/)
        arr.forEach((item) => {
          let arr = item.split(/=|%3D/)
          let key = arr[0]
          let val = arr[1]
          this.urlParamMap[key] = val
          this.urlParamKeyArr.push(key)
        })
      }
    }
    add(key: string, val: string) {
      if (!this.get(key)) {
        this.urlParamMap[key] = val
        this.urlParamKeyArr.push(key)
      } else {
        this.urlParamMap[key] = val
      }
    }
    del(key: string) {
      let index = this.urlParamKeyArr.indexOf(key)
      if (index !== -1) {
        delete this.urlParamMap[key]
        this.urlParamKeyArr.splice(index, 1)
      }
    }
    get(name: string) {
      return this.urlParamMap[name]
    }
    keys() {
      return this.urlParamKeyArr
    }
  }

  const U = new UrlParam()

  const APP_ID = 'appId'
  const ADV_CHANNEL = 'advChannel'
  const SDK_VERSION = 'sdkVersion'
  const SHORTCUT = 'shortcut'
  const DEBUGGER = 'debugger'
  const SDK_HASH = 't'

  let user: string = localStorage.getItem('user')
  let users: string = localStorage.getItem('users')

  try {
    user = JSON.parse(user)
  } catch (e) {
    localStorage.removeItem('user')
    user = null
  }

  try {
    users = JSON.parse(users)
  } catch (e) {
    localStorage.removeItem('users')
    users = null
  }


  !user && localStorage.removeItem('user')
  !users && localStorage.removeItem('users')

  const obj = U.get('user') ? JSON.parse(decodeURIComponent(U.get('user'))) : {}
  if (obj.user) {
    localStorage.setItem('user', JSON.stringify(obj.user))
  }
  if (obj.users) {
    if (users) {
      localStorage.setItem('users', JSON.stringify(Object.assign(users, obj.users)))
    } else {
      localStorage.setItem('users', JSON.stringify(obj.users))
    }
  }

  U.del('user')

  const origin = U.get(DEBUGGER) ? sdk_origin_test : sdk_origin
  U.get(SDK_VERSION) || U.add(SDK_VERSION, sdk_version)
  U.get(APP_ID) || U.add(APP_ID, app_id)
  U.get(ADV_CHANNEL) || U.add(ADV_CHANNEL, adv_channel)
  U.get(SDK_HASH) || U.add(SDK_HASH, sdk_hash)

  const index = document.createElement('script')
  index.src = `${origin}/jssdk/${U.get(SDK_VERSION)}/index.js?${U.get(SDK_HASH)}`
  index.onload = function () {
    $rg_index({
      U,
      origin,
      iframe: document.querySelector('iframe'),
    })

    const giftCodeDefault = 'Please download the game to the desktop first, then click on the icon to open game and click the gift code again to get the reward.'
    const giftCode = 'UAN6GN6Y'
    const btnGiftCode = document.querySelector('.gift-code')
    const clickGiftCode = function () {
      codeBox.style.visibility = codeBox.style.visibility === 'hidden' ? 'visible' : 'hidden'
    }
    btnGiftCode.addEventListener('click', clickGiftCode)
    const codeBox = document.querySelector('.code-box') as HTMLDivElement
    if (U.get(SHORTCUT)) {
      codeBox.style.textIndent = '0'
      codeBox.innerHTML = giftCode
    } else {
      codeBox.innerHTML = giftCodeDefault
    }
    const link = `${origin}/platform/shortcut.jsp?link=${encodeURIComponent(location.origin + location.pathname + '?shortcut=true')}&fileName=PokemonQuest`;
    (window as any).shortcutAdd = function () {
      console.log('URTIKGFODKE:D::')
      window.open(link)
    }
    if (window.name === 'install') {
      window.open(link)
      window.name = ''
    }

  }
  const s = document.getElementsByTagName('script')[0]
  s.parentNode.insertBefore(index, s);

}




